  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.header {
  width: 100%;
  z-index: 10;
  background-color: var(--light-button-text-color);
  position: fixed;
  top: 0;
  left: 0;
}

.header:before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: var(--light-button-text-color);
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 8px 20px rgba(134, 229, 67, .8), 0 4px 10px rgba(255, 255, 255, .1);
}

.header__body {
  height: 136px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__logo {
  z-index: 3;
  position: relative;
  overflow: hidden;
}

.header__burger {
  cursor: pointer;
  background-color: inherit;
  border: none;
  display: none;
}

.header__list {
  z-index: 3;
  justify-content: space-between;
  display: flex;
  position: relative;
}

.header__list li:not(:last-child) {
  margin-right: 48px;
}

.header__link {
  color: var(--secondary-text-color);
  border-bottom: 1px solid rgba(0, 0, 0, 0);
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 16px;
  line-height: 1;
  transition: border-color .5s cubic-bezier(.4, 0, .2, 1) .1s;
}

.header__link:focus, .header__link:hover {
  border-color: #86e543;
}

.header__button {
  background-color: var(--primary-text-color);
  text-align: right;
  color: var(--light-button-text-color);
  z-index: 3;
  border: rgba(0, 0, 0, 0);
  border-radius: 16px;
  padding: 16px 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  position: relative;
}

.header__button:focus, .header__button:hover {
  box-shadow: 0 8px 20px rgba(134, 229, 67, .5), 0 4px 10px rgba(255, 255, 255, .1);
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }

  .header__body {
    height: 104px;
  }

  .header__burger {
    width: 54px;
    height: 36px;
    z-index: 3;
    display: block;
    position: relative;
  }

  .header__burger span {
    background-color: var(--primary-text-color);
    width: 100%;
    height: 3.6px;
    transition: all .5s ease-out;
    position: absolute;
    top: 17px;
    left: 0;
  }

  .header__burger:before, .header__burger:after {
    content: "";
    background-color: var(--primary-text-color);
    width: 100%;
    height: 3.6px;
    transition: all .5s ease-out;
    position: absolute;
    left: 0;
  }

  .header__burger:before {
    top: 0;
  }

  .header__burger:after {
    bottom: 0;
  }

  .header__burger.active span {
    transform: scale(0);
  }

  .header__burger.active:before {
    top: 2px;
    transform: rotate(-45deg)translate(-6px, 6px);
  }

  .header__burger.active:after {
    bottom: 12px;
    transform: rotate(45deg)translate(-6px, -8px);
  }

  .header__menu {
    width: 100%;
    height: 100%;
    background-image: url("background-menu.97074f91.jpg");
    background-color: var(--light-button-text-color);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 70px 0 0;
    transition: all .5s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: -100%;
    left: 0;
    overflow: auto;
  }
}

